/**
 * Global Reset of all HTML Elements
 *
 * Resetting all of our HTML Elements ensures a smoother
 * visual transition between browsers. If you don't believe me,
 * try temporarily commenting out this block of code, then go
 * and look at Mozilla versus Safari, both good browsers with
 * a good implementation of CSS. The thing is, all browser CSS
 * defaults are different and at the end of the day if visual
 * consistency is what we're shooting for, then we need to
 * make sure we're resetting all spacing elements.
 *
 */
html, body {
  border: 0;
  font-family: "Helvetica-Neue", "Helvetica", Arial, sans-serif;
  line-height: 1.5;
  margin: 0;
  padding: 0; }

div, span, object, iframe, img, table, caption, thead, tbody,
tfoot, tr, tr, td, article, aside, canvas, details, figure, hgroup, menu,
nav, footer, header, section, summary, mark, audio, video {
  border: 0;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cit, code,
del, dfn, em, ins, q, samp, small, strong, sub, sup, b, i, hr, dl, dt, dd,
ol, ul, li, fieldset, legend, label {
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  margin: 0;
  padding: 0; }

article, aside, canvas, figure, figure img, figcaption, hgroup,
footer, header, nav, section, audio, video {
  display: block; }

table {
  border-collapse: separate;
  border-spacing: 0; }
  table caption, table th, table td {
    text-align: left;
    vertical-align: middle; }

a img {
  border: 0; }

:focus {
  outline: 0; }

@font-face {
  font-family: 'HelveticaNeueCyr Thin';
  src: url("../fonts/HelveticaNeueCyr-Thin.eot");
  src: local("HelveticaNeueCyr-Thin"), url("../fonts/HelveticaNeueCyr-Thin.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaNeueCyr-Thin.woff") format("woff"), url("../fonts/HelveticaNeueCyr-Thin.ttf") format("truetype");
  font-weight: 300;
  font-style: normal; }

.main-wrapper {
  position: relative;
  z-index: 10;
  background: #fefefe url("../img/purple_sunset-wallpaper-2880x1800.png") no-repeat center center fixed;
  background-size: cover;
  width: 100vw;
  height: 100vh; }
  .main-wrapper:before {
    position: absolute;
    content: "";
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); }

.login-form {
  width: 630px;
  height: 435px;
  background: inherit;
  position: absolute;
  overflow: hidden;
  top: 50%;
  left: 50%;
  margin-left: -315px;
  margin-top: -220px;
  border-radius: 4px;
  box-shadow: 2px 0 32px rgba(0, 0, 0, 0.5); }
  @media (min-width: 320px) and (max-width: 475px) {
    .login-form {
      width: 300px;
      margin-left: -149px; } }
  .login-form:before {
    width: 680px;
    height: 440px;
    content: "";
    position: absolute;
    top: 0;
    left: -25px;
    bottom: 0;
    right: 0;
    background: inherit;
    box-shadow: inset 0 0 0 275px rgba(255, 255, 255, 0.2);
    filter: blur(10px); }
  .login-form__wrapper {
    text-align: center;
    width: 367px;
    position: relative;
    margin: 0 auto;
    padding-top: 21px; }
    @media (min-width: 320px) and (max-width: 475px) {
      .login-form__wrapper {
        width: 290px; } }
    .login-form__wrapper--img {
      margin-bottom: 15px; }
    .login-form__wrapper--row {
      margin-bottom: 14px;
      display: flex;
      justify-content: space-between; }
      .login-form__wrapper--row:before {
        clear: both; }
    .login-form__wrapper--link {
      padding: 5px 24px; }
      @media (min-width: 320px) and (max-width: 475px) {
        .login-form__wrapper--link {
          font-size: 14px; } }
    .login-form__wrapper--ok {
      background: url("../img/ok.png") no-repeat 95% 13px; }
    .login-form__wrapper--err {
      background: url("../img/err.png") no-repeat 95% 8px; }
      .login-form__wrapper--err input {
        color: #b40000; }
    .login-form__wrapper--button {
      margin: 0 auto;
      padding: 16px 49px;
      background: transparent;
      outline: unset;
      border: 1px solid #fff;
      border-radius: 27px;
      font-size: 16px;
      color: #fff;
      letter-spacing: 1px; }
      .login-form__wrapper--button:hover, .login-form__wrapper--button:active {
        background: #C0392B; }
      .login-form__wrapper--button:disabled {
        background: transparent; }
    .login-form__wrapper--input {
      position: relative;
      padding: 0;
      width: 100%;
      font-size: 18px;
      border: 1px solid #ffffff;
      line-height: 47px;
      border-radius: 27px;
      text-align: center;
      background-color: transparent;
      transition: all 0.4s ease; }
      .login-form__wrapper--input:hover, .login-form__wrapper--input:active {
        background-color: #ffffff;
        border: 1px solid transparent; }
      .login-form__wrapper--input::placeholder {
        color: #d3d3d3; }
    .login-form__wrapper--check {
      width: 43px;
      height: 23px;
      float: left;
      border: 1px solid #fff;
      margin: 2px 18px;
      display: inline-block;
      position: relative;
      border-radius: 50px; }
      .login-form__wrapper--check span {
        margin-left: 50px;
        color: #fff;
        vertical-align: text-top;
        letter-spacing: 0.5px; }
        @media (min-width: 320px) and (max-width: 475px) {
          .login-form__wrapper--check span {
            font-size: 14px; } }
      .login-form__wrapper--check label {
        display: block;
        width: 22px;
        height: 16px;
        position: absolute;
        top: 4px;
        left: 4px;
        cursor: pointer;
        background: #fcfff4;
        border-radius: 50px;
        transition: all 0.4s ease; }
      .login-form__wrapper--check input[type=checkbox] {
        visibility: hidden; }
        .login-form__wrapper--check input[type=checkbox]:checked + label {
          left: 18px; }
    .login-form__wrapper span a {
      color: rgba(255, 255, 255, 0.8); }

.breadcrumb {
  position: relative;
  top: 23px;
  left: 55px;
  letter-spacing: 1.2px;
  display: inline-block;
  font-family: 'HelveticaNeueCyr Thin', sans-serif;
  font-size: 10px; }
  .breadcrumb span {
